'use strict';

import * as gulp from 'gulp';
import * as gutil from 'gulp-util';
import * as path from 'path';
import * as through2 from 'through2';

import { GulpTask, IBuildConfig } from '@microsoft/gulp-core-build';
import ISolution from './packageSolution/models/packageDefinition/ISolution';
import createPackage from './packageSolution/createPackage';
import { IClientSideComponentManifest } from '@microsoft/sp-module-interfaces';
import ManifestValidator,
       { IManifestValidatorResult } from '@microsoft/sp-module-interfaces/lib/manifestSchemaValidator';

export interface IPackageSolutionTaskConfig {
  paths?: {
    /**
     * The packaging root folder. Defaults to './sharepoint'
     */
    packageDir?: string;

    /**
     * The folder to write the raw package to disk for debugging. Defaults to 'solution/debug'
     */
    debugDir?: string;

    /**
     * The name of the sppkg to create (including extension) Defaults to 'ClientSolution.sppkg'
     */
    zippedPackage?: string;

    /**
     * The folder containing the raw feature_xml to import into the package. Defaults to 'feature_xml'
     */
    featureXmlDir?: string;

    /**
     * The glob to match against to find manifest files. Looks in dist/ when running in normal, but deploy/ for
     *  production
     */
    manifestsMatch?: string;

    /**
     * The path to the folder where manifests are stored. Defaults to buildConfig.distFolder
     */
    manifestDir?: string;
  };

  /**
   * The definition of this solution's package, features, and components
   */
  solution?: ISolution;
}

export class PackageSolutionTask extends GulpTask<IPackageSolutionTaskConfig> {
  public name: string = 'package-solution';

  public taskConfig: IPackageSolutionTaskConfig = {
    paths: {
      packageDir: 'sharepoint',
      debugDir: path.join('solution', 'debug'),
      zippedPackage: path.join('solution', 'ClientSolution.sppkg'),
      featureXmlDir: 'feature_xml',
      manifestsMatch: '*.json'
    },
    solution: {
      'name': 'A Sample Solution',
      'id': '00000000-0000-0000-0000-000000000000'
    }
  };

  private _hasPrependedPaths: boolean = false;

  public executeTask(gulp: gulp.Gulp, completeCallback: () => void): void {
    if (!this.taskConfig.paths.manifestDir) {
      this.taskConfig.paths.manifestDir = this.buildConfig.distFolder;
    }

    this._prependBaseToConfigPaths();

    const manifestMap: Map<string, IClientSideComponentManifest> = new Map<string, IClientSideComponentManifest>();
    let foundAnyManifests: boolean = false;
    gulp.src(this.taskConfig.paths.manifestsMatch)
        .pipe(through2.obj((file: gutil.File, enc: string, callback: () => void) => {
          foundAnyManifests = true;
          const manifest: IClientSideComponentManifest =
            JSON.parse((file.contents as Buffer).toString(enc));

          const validationResult: IManifestValidatorResult = ManifestValidator.validateManifest(manifest);
          if (!validationResult.result) {
            this.log(`Ignoring manifest due to validation error: (${file.path}) use '--verbose' for more information`);
            this.logVerbose(ManifestValidator.getFormattedErrorMessage(validationResult.errors));
            callback();
            return;
          }

          this.log(`Found manifest: ${file.path}`);
          manifestMap.set(manifest.id, manifest);
          callback();
        },
        (callback: (err?: Error | string | void) => void) => {
          if (!manifestMap.size) {
            if (foundAnyManifests) {
              callback(new Error(`Some manifests were found in '${this.taskConfig.paths.manifestsMatch}' ` +
                       ', but were not valid. Ensure paths.manifestsMatch is correct'));
            } else {
              callback(new Error(`No manifests were found in '${this.taskConfig.paths.manifestsMatch}'. ` +
                       'Ensure a build has completed successfully and that paths.manifestsMatch is correct'));
            }
          }
          createPackage(this.taskConfig, manifestMap).then(callback);
        }))
        .on('finish', () => completeCallback());
  }

  public getCleanMatch(buildConfig: IBuildConfig): string[] {
    this.buildConfig = buildConfig;

    if (!this.taskConfig.paths.manifestDir) {
      this.taskConfig.paths.manifestDir = this.buildConfig.distFolder;
    }

    this._prependBaseToConfigPaths();
    return [
      this.taskConfig.paths.debugDir,
      this.taskConfig.paths.zippedPackage
    ];
  }

  private _prependBaseToConfigPaths(): void {
    if (!this._hasPrependedPaths) {
      this.taskConfig.paths.manifestsMatch =
        path.join(this.taskConfig.paths.manifestDir, this.taskConfig.paths.manifestsMatch);
      this.taskConfig.paths.debugDir =
        path.join(this.taskConfig.paths.packageDir, this.taskConfig.paths.debugDir);
      this.taskConfig.paths.zippedPackage =
        path.join(this.taskConfig.paths.packageDir, this.taskConfig.paths.zippedPackage);
      this.taskConfig.paths.featureXmlDir =
        path.join(this.taskConfig.paths.packageDir, this.taskConfig.paths.featureXmlDir);
      this._hasPrependedPaths = true;
    }
  }
}
